<template>
  <va-card :title="$t('tables.labelRestriction')" class="w-100">
    <va-button
      icon="fa fa-plus"
      flat
      slot="actions"
      class="mr-0"
      @click="showCreateModal()">
    </va-button>
    <div class="row mr-0">
      <div class="flex xs12 sm4">
        <va-date-picker
          :label="$t('forms.dateTimePicker.from')"
          v-model="search.from"
        />
      </div>
      <div class="flex xs12 sm4">
        <va-date-picker
          :label="$t('forms.dateTimePicker.to')"
          v-model="search.to"
        />
      </div>
      <va-button
        icon="fa fa-trash"
        flat
        slot="actions"
        color="danger"
        class="mr-0"
        @click="deletesearch"
      />
      <va-button
        icon="fa fa-search"
        flat
        slot="actions"
        color="success"
        class="mr-0"
        @click="searchDate"
      />
    </div>
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      :totalPages="totalPages"
      @page-selected="readItems"
      api-mode
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{(props.rowIndex + 1) + ((pageNumber  -1) *10) }}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="name_Restrictions_Type" slot-scope="props">
        {{ props.rowData.restrictions_Type.name}}
      </template>
      <template slot="name_Restrictions_State" slot-scope="props">
        {{ props.rowData.restrictions_State.name}}
      </template>
      <template slot="actions" slot-scope="props">
        <va-button flat small color="info" @click="showViewModal(props.rowData)" class="ma-0">
          {{ $t('tables.view') }}
        </va-button>
        <va-button flat small color="gray" v-if="props.rowData.restrictions_State.id !== 1" @click="showEditModal(props.rowData)" class="ma-0">
          {{ $t('tables.edit') }}
        </va-button>
      </template>
    </va-data-table>

    <va-modal v-model="ViewModal"
      size="large"
      :title=" $t('generic.restrictionsDetailes') "
      class="viewNodel"
      hide-default-actions >
      <div class="row W100">
        <div class="flex xs12 md12">
          <div class="row row-separated">
            <div class="flex xs3">
              <p class="display-3 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobjectView.id}}</p>
              <p class="text--center mb-1">{{ $t('tables.headings.restrictionNumber')}}</p>
            </div>
            <div class="flex xs3">
              <p class="display-3 mb-1 text--center" :style="{color: this.$themes.info}">{{formatDate( this.currentobjectView.date , false)}}</p>
              <p class="text--center no-wrap mb-1">{{$t('tables.headings.date') }}</p>
            </div>
            <div class="flex xs3">
              <p class="display-3 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobjectView.restrictions_Type.name}}</p>
              <p class="text--center mb-1">{{ $t('tables.headings.name_Restrictions_Type') }}</p>
            </div>
            <div class="flex xs3">
              <p class="display-3 mb-1 text--center" :style="{color: this.$themes.info}">{{ this.currentobjectView.restrictions_State.name}}</p>
              <p class="text--center mb-1">{{ $t('tables.headings.name_Restrictions_State')}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <va-data-table style="width: 100%;"
          :fields="restrictions_Sub_fields"
          :data="currentobjectView.restrictions_Sub"
          api-mode
          no-pagination
        >
          <template slot="marker" slot-scope="props">
            {{props.rowIndex + 1}}
          </template>
          <template slot="accounts" slot-scope="props">
            {{ props.rowData.accounts.name}}
          </template>
        </va-data-table>

      </div>
      <div class=" text--center">
        <p class="display-2 mb-1 text--center" :style="this.currentobjectView.totalDeabit==this.currentobjectView.totalCridt ? {color: this.$themes.success} :{color: this.$themes.danger}">{{ this.currentobjectView.totalDeabit==this.currentobjectView.totalCridt ? $t('modal.balancedRestrictions') :$t('modal.unbalancedRestrictions') }}</p>

      </div>
    </va-modal>

    <va-modal v-model="deleteModal" no-outside-dismiss
      :title=" $t('generic.deleteItemTitle') "
      :okText=" $t('modal.delete') "
      :cancelText=" $t('modal.cancel') "
      :message=" $t('generic.deleteItemMsg') "
      @ok="okDeletefun()"
      @cancel="cancleDeletefun()">
    </va-modal>

    <va-modal v-model="createModal" no-outside-dismiss
      size="large"
      :title="$t(modelTitle)"
      hide-default-actions >
      <form @submit.prevent="editModal? okEditfun()  :okCreatefun()">
        <div class="row flex-center">
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.date')+' *'}} </label>
            <va-date-picker
              :label="$t('placeholder.date')"
              v-model="currentobject.date"
              required
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.notes')}} </label>
            <va-input v-model="currentobject.notes"
              :placeholder="$t('placeholder.notes')" />
          </div>

          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.branchs')+' *'}} </label>
            <va-select
              :label="$t('placeholder.branchs')"
              v-model="fK_Branchs.value"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fK_Branchs.options"
            />
          </div>
          <div class="flex md6 sm12 xs12 px-1">
            <label class="input-label"> {{$t('placeholder.CompanyActivity') +' *'}} </label>
            <va-select
              :label="$t('placeholder.CompanyActivity')"
              v-model="fK_Company_Activity.value"
              required
              searchable
              textBy="name"
              keyBy="id"
              :options="fK_Company_Activity.options"
            />
          </div>
        </div>
        <va-card :title="$t('tables.labelRestrictionsSub')" class="w-100">
          <div class="row flex-center">
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.credit')+' *'}} </label>
              <va-input v-model="newSub.credit" type="number" min="0"
                :placeholder="$t('placeholder.credit')" />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.dept')+' *'}} </label>
              <va-input v-model="newSub.dept" type="number" min="0"
                :placeholder="$t('placeholder.dept')" />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.notes')}} </label>
              <va-input v-model="newSub.notes"
                :placeholder="$t('placeholder.notes')" />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <label class="input-label"> {{$t('placeholder.AccountName')+' *'}} </label>
              <va-select
                :label="$t('placeholder.AccountName')"
                v-model="fK_Accounts.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fK_Accounts.options"
              />
            </div>
            <div class="d-flex justify--center mt-3">
              <va-button type="button"  color="success"  flat @click="addToSubsfun()">
                {{ $t('modal.add') }}
              </va-button>
            </div>
          </div>
          <div class="row">
            <va-data-table style="width: 100%;"
              :fields="add_restrictions_Sub_fields"
              :data="currentobject.restrictions_Sub"
              api-mode
              no-pagination
            >
              <template slot="marker" slot-scope="props">
                {{props.rowIndex + 1}}
              </template>
              <template slot="accounts" slot-scope="props">
                {{ props.rowData.accounts.name}}
              </template>
              <template slot="actions" slot-scope="props">
                <va-button flat small color="danger" @click="deletafromsubs(props.rowIndex)" class="ma-0">
                  {{ $t('tables.delete') }}
                </va-button>
              </template>
            </va-data-table>
          </div>
        </va-card>

        <div class="d-flex justify--center mt-3">
          <va-button type="button" color="gray" flat @click="editModal? cancleEditfun() : cancleCreatefun()">
            {{ $t('modal.cancel') }}
          </va-button>
          <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t(modelOktext) }}</va-button>
        </div>
      </form>

    </va-modal>
  </va-card>

</template>

<script>
import RestrictionService from '../../../../services/finances/RestrictionService'
// import RestrictionsTypeService from '../../../../services/finances/RestrictionsTypeService'
import RestrictionsStateService from '../../../../services/finances/RestrictionsStateService'
import BranchsService from '../../../../services/BasicData/BranchsService'
import CompanyActivityService from '../../../../services/BasicData/CompanyActivityService'
import AccountsService from '../../../../services/finances/AccountsService'

import helper from '../../../../helper/helper'
export default {
  data () {
    return {
      search: {},
      index: 0,
      perPage: 10,
      totalPages: 0,
      dataTable: [],
      ViewModal: false,
      loading: true,
      modelTitle: '',
      modelOktext: '',
      stopBtn: false,
      deleteModal: false,
      editModal: false,
      createModal: false,
      pageNumber: 1,
      currentobjectView: {
        restrictions_State: {},
        restrictions_Type: {},
      },
      opjectBeforeEdit: {},
      currentobject: {
        date: '',
        restrictions_Sub: [
        ],
      },
      newSub: {},
      fK_Branchs: {
        value: '',
        options: [],
      },
      fK_Restrictions_Type: {
        value: '',
        options: [],
      },
      fK_Restrictions_State: {
        value: '',
        options: [],
      },
      fK_Company_Activity: {
        value: '',
        options: [],
      },
      fK_Accounts: {
        value: '',
        options: [],
      },
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'id',
        dataClass: 'text-center',
        title: this.$t('tables.headings.restrictionNumber'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|id',
      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
        sortField: 'name',
        sortCallBack: 'sortCallBack|date',
      },
      {
        name: '__slot:name_Restrictions_Type',
        title: this.$t('tables.headings.name_Restrictions_Type'),
      },

      {
        name: '__slot:name_Restrictions_State',
        title: this.$t('tables.headings.name_Restrictions_State'),
      },
      {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      },
      {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
    restrictions_Sub_fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'dept',
        dataClass: 'text-center',
        title: this.$t('tables.headings.dept'),
      }, {
        name: 'credit',
        title: this.$t('tables.headings.credit'),
      },
      {
        name: 'code_Accounts',
        title: this.$t('tables.headings.code_Accounts'),
      },
      {
        name: '__slot:accounts',
        title: this.$t('tables.headings.name_Accounts'),
      }, {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      }]
    },
    add_restrictions_Sub_fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'dept',
        dataClass: 'text-center',
        title: this.$t('tables.headings.dept'),
      }, {
        name: 'credit',
        title: this.$t('tables.headings.credit'),
      },
      {
        name: '__slot:accounts',
        title: this.$t('tables.headings.name_Accounts'),
      }, {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      }, {
        name: '__slot:actions',
        dataClass: 'text-right',
      }]
    },
  },
  created () {
    this.readItems()
    // this.getTypes()
    this.getStates()
    this.getCompanyActivity()
    this.getBranchs()
    this.getAccounts()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api Padination */
    sortCallBack (obj) {
      this.readItems(1, (obj[0].toString().charAt(0).toUpperCase() + obj[0].toString().slice(1)))
    },
    readItems (page = 1, orderBy = null) {
      this.pageNumber = page
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        OrderBy: orderBy,
      }
      RestrictionService.getAllPagnation(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    searchItems (page = 1) {
      this.loading = true
      console.log('fkghg')
      const params = {
        PageSize: this.perPage,
        PageNumber: page,
        dateFrom: this.search.from,
        dateTo: this.search.to,
      }
      RestrictionService.SearchByDates(params)
        .then(response => {
          this.dataTable = response.data
          var pagination = JSON.parse(response.headers['x-pagination'])
          this.totalPages = pagination.TotalPages
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    // getTypes () {
    //   RestrictionsTypeService.getAll()
    //     .then(response => {
    //       this.fK_Restrictions_Type.options = response.data
    //     },
    //     err => {
    //       console.error(err)
    //       this.loading = false
    //       this.showToast(this.$t('generic.error'), {
    //         icon: 'fa-exclamation',
    //         position: 'bottom-center',
    //         duration: 2500,
    //         type: 'error',
    //       })
    //     })
    // },
    getStates () {
      RestrictionsStateService.getAll()
        .then(response => {
          this.fK_Restrictions_State.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getCompanyActivity () {
      CompanyActivityService.getAll()
        .then(response => {
          this.fK_Company_Activity.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getBranchs () {
      BranchsService.getAll()
        .then(response => {
          this.fK_Branchs.options = response.data
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        })
    },
    getAccounts () {
      AccountsService.getAll().then(
        (response) => {
          this.fK_Accounts.options = response.data
        },
        (err) => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',
          })
        },
      )
    },
    /* end api Padination */

    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },
    searchDate () {
      this.searchItems(1)
    },

    deletesearch () {
      this.search = {}
      this.readItems()
    },

    /* cud api */
    create () {
      if (this.fK_Company_Activity.value !== '' && this.fK_Company_Activity.value !== null && this.fK_Branchs.value !== '' && this.fK_Branchs.value !== null) {
        this.currentobject.fK_Branchs = this.fK_Branchs.value.id
        this.currentobject.fK_Restrictions_Type = 7
        this.currentobject.fK_Company_Activity = this.fK_Company_Activity.value.id
        this.currentobject.fK_Restrictions_State = 2
      } else {
        this.stopBtn = false
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      var totalCridtSubs = 0.0
      var totaldeptSubs = 0.0
      this.currentobject.restrictions_Sub.forEach(x => {
        totalCridtSubs = parseFloat(totalCridtSubs + parseFloat(x.credit))
        totaldeptSubs = parseFloat(totaldeptSubs + parseFloat(x.dept))
      })
      if (totalCridtSubs !== totaldeptSubs) {
        this.stopBtn = false
        return this.showToast(this.$t('modal.unbalancedRestrictions'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      RestrictionService.create(this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.loading = false
          this.currentobject = {
            date: '',
            restrictions_Sub: [
            ],
          }
          this.fK_Branchs.value = ''
          this.fK_Company_Activity.value = ''
          this.fK_Restrictions_State.value = ''
          this.fK_Accounts.value = ''
          this.createModal = false
          this.stopBtn = false

          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    edit () {
      if (this.fK_Company_Activity.value !== '' && this.fK_Company_Activity.value !== null && this.fK_Branchs.value !== '' && this.fK_Branchs.value !== null) {
        this.currentobject.fK_Branchs = this.fK_Branchs.value.id
        this.currentobject.fK_Restrictions_Type = 7
        this.currentobject.fK_Company_Activity = this.fK_Company_Activity.value.id
        this.currentobject.fK_Restrictions_State = 2
      } else {
        this.stopBtn = false
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      var totalCridtSubs = 0.0
      var totaldeptSubs = 0.0
      this.currentobject.restrictions_Sub.forEach(x => {
        x.fK_Accounts = x.accounts.id
        totalCridtSubs = parseFloat(totalCridtSubs + x.credit)
        totaldeptSubs = parseFloat(totaldeptSubs + x.dept)
      })
      if (totalCridtSubs !== totaldeptSubs) {
        this.stopBtn = false
        return this.showToast(this.$t('modal.unbalancedRestrictions'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
      RestrictionService.update(this.currentobject.id, this.currentobject).then(response => {
        if (response.headers['x-status'] === 'true') {
          this.readItems()
          this.currentobject = {
            date: '',
            restrictions_Sub: [
            ],
          }
          this.fK_Branchs.value = ''
          this.fK_Company_Activity.value = ''
          this.fK_Accounts.value = ''
          this.loading = false
          this.editModal = false
          this.createModal = false
          this.stopBtn = false
          this.showToast(this.$t('generic.done'), {
            icon: 'fa-check',
            position: 'bottom-center',
            duration: 2500,
            type: 'success',
          })
        } else {
          this.loading = false
          this.stopBtn = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-center',
            duration: 2500,
            type: 'error',

          })
        }
      },
      err => {
        console.error(err)
        this.loading = false
        this.stopBtn = false
        this.showToast(this.$t('generic.error'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      })
    },
    /* cud api */

    /* viwe model */
    showViewModal (cuobject) {
      this.ViewModal = true
      this.currentobjectView = cuobject
      this.currentobjectView.totalCridt = 0.0
      this.currentobjectView.totalDeabit = 0.0
      cuobject.restrictions_Sub.forEach(element => {
        this.currentobjectView.totalCridt = parseFloat(this.currentobjectView.totalCridt + element.credit)
        this.currentobjectView.totalDeabit = parseFloat(this.currentobjectView.totalDeabit + element.dept)
      })
    },
    /* end view model */

    /* edit model */
    showEditModal (cuobject) {
      this.editModal = true
      this.modelTitle = 'generic.editItemTitle'
      this.modelOktext = 'modal.edit'
      this.createModal = true
      this.fK_Branchs.value = cuobject.branchs === null ? '' : cuobject.branchs
      this.fK_Company_Activity.value = cuobject.company_Activity === null ? '' : cuobject.company_Activity
      this.opjectBeforeEdit = Object.assign({}, cuobject)
      this.currentobject = cuobject
      this.loading = true
    },
    okEditfun () {
      this.stopBtn = true
      this.edit()
    },
    cancleEditfun () {
      Object.assign(this.currentobject, this.opjectBeforeEdit)
      this.createModal = false
      this.loading = false
      this.currentobject = {
        date: '',
        restrictions_Sub: [
        ],
      }
      this.fK_Branchs.value = ''
      this.editModal = false
      this.stopBtn = false
    },
    /* end edit model */

    /* create model */
    showCreateModal () {
      this.currentobject.date = helper.todayDate()
      this.modelTitle = 'generic.creatItemTitle'
      this.modelOktext = 'modal.create'
      this.createModal = true
      this.loading = true
    },
    okCreatefun () {
      this.stopBtn = true
      this.create()
    },
    cancleCreatefun () {
      this.createModal = false
      this.loading = false
      this.currentobject = {
        date: '',
        restrictions_Sub: [
        ],
      }
      this.fK_Branchs.value = ''
      this.fK_Company_Activity.value = ''
      this.fK_Accounts.value = ''
      this.stopBtn = false
    },
    /* end create model */

    /* add and delete from new sub */
    addToSubsfun () {
      if (this.fK_Accounts.value !== '' && this.fK_Accounts.value !== null) {
        if (this.newSub.credit === null || this.newSub.credit === undefined || this.newSub.credit === '') {
          this.newSub.credit = 0
        }
        if (this.newSub.dept === null || this.newSub.dept === undefined || this.newSub.dept === '') {
          this.newSub.dept = 0
        }
        this.newSub.fK_Accounts = this.fK_Accounts.value.id
        this.newSub.accounts = this.fK_Accounts.value
        this.currentobject.restrictions_Sub.push(this.newSub)
        this.newSub = {}
        this.fK_Accounts.value = ''
      } else {
        return this.showToast(this.$t('generic.filldata'), {
          icon: 'fa-exclamation',
          position: 'bottom-center',
          duration: 2500,
          type: 'error',
        })
      }
    },
    deletafromsubs (index) {
      this.currentobject.restrictions_Sub.splice(index, 1)
    },
    /* endadd and delete from new sub */
  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

  .viewNodel .va-modal {
    width: 100%;
  }

</style>
