import http from '../../http-common'

class RestrictionsStateervice {
  getAll () {
    return http.get('RestrictionsState')
  }

  getAllPagnation (params) {
    return http.get('RestrictionsState', { params })
  }

  get (id) {
    return http.get(`RestrictionsState/getById?Id=${id}`)
  }

  search (params) {
    return http.get('RestrictionsState/Search', { params })
  }

  create (data) {
    return http.post('RestrictionsState', data)
  }

  update (id, data) {
    return http.put(`RestrictionsState?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`RestrictionsState?Id=${id}`)
  }
}

export default new RestrictionsStateervice()
